import {
  Component,
  Inject
} from '@angular/core';
import  { AuthService} from '../../../services/auth';
import  {Router, ROUTER_DIRECTIVES} from "@angular/router";
@Component({
  selector: 'nav-bar',
  template: require('./client/dev/app/components/back-end/shared/templates/nav-bar.html'),
  styles: [require('./client/dev/asserts/css/admin.css')],
  directives: [
    ROUTER_DIRECTIVES
  ]
})
export class NavbarComponent {
  constructor(private _auth: AuthService, private router: Router){

  }
  logout(): void {
    this._auth.logout();
    this._auth.logoutClient();
    this.router.navigateByUrl('/kshare');
  }
}



